.nav-container {
  display: flex;
  padding: 1rem 2rem 1rem 2rem;
  overflow-x: scroll;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
  .nav-item {
    margin: 0 2rem;

    .nav-link {
      padding: 0;
      margin: 0;
      color: grey;
      text-decoration: none;
      font-size: 17px;
      transform: scale(1);
      cursor: default;
      &:hover {
        color: white;
        transform: scale(1.2);
        transition: color 500ms, transform 400ms;
      }
    }

    .active-navlink {
      color: white !important;
      &:hover {
        transform: none !important;
        transition: none !important;
      }
    }
  }
}

.nav-container::-webkit-scrollbar {
  display: none;
}
